Vue.filter('formatDate',(str)=>{
  if(!str) return''
  let date = new Date(str)
  let time = (new Date().getTime() - date.getTime())
  if(time < 0){
    return ''
  }else if((time/1000) < 30){
    return 'ÂàöÂàö'
  }else if((time/1000) < 60){
    return parseInt(time/1000) + 'ÁßíÂâç'
  }else if((time/60000) < 60){
    return parseInt(time/60000) + 'ÂàÜÈíüÂâç'
  }else if((time/3600000) < 24){
    return parseInt(time/3600000) + 'Â∞èÊó∂Ââç'
  }else if((time/86400000) < 30){
    return parseInt(time/86400000) + 'Â§©Ââç'
  }else if((time/2592000000) < 12){
    return parseInt(time/2592000000) + '‰∏™ÊúàÂâç'
  }else{
    return parseInt(time/32140800000) + 'Âπ¥Ââç'
  }
})
Vue.filter('formatType',(post)=>{
  if(post.good == true){
    return "Á≤æÂçé"
  }else if(post.top == true){
    return "ÁΩÆÈ°∂"
  }else if(post.tab == 'ask'){
    return "ÈóÆÁ≠î"
  }else if(post.tab == 'share'){
    return "ÂàÜ‰∫´"
  }else{
    return "ÊãõËÅò"
  }
})
new Vue({
  el: '#app',
  router,
  components:{ App },
  render: h => h(App)
})
